import React, { useState } from 'react';

const CalcSection = () => {
    const [weight, setWeight] = useState('');
    const [height, setHeight] = useState('');
    const [bmi, setBmi] = useState(null);
    const [category, setCategory] = useState('');

    const calculateBMI = () => {
        if (!weight || !height) {
            alert('Пожалуйста, введите вес и рост');
            return;
        }

        const weightNum = parseFloat(weight);
        const heightNum = parseFloat(height) / 100; // переводим см в метры
        const bmiValue = weightNum / (heightNum * heightNum);
        const roundedBmi = bmiValue.toFixed(1);

        setBmi(roundedBmi);

        // Определение категории
        if (bmiValue < 18.5) {
            setCategory('Недостаточный вес');
        } else if (bmiValue < 25) {
            setCategory('Нормальный вес');
        } else if (bmiValue < 30) {
            setCategory('Избыточный вес');
        } else if (bmiValue < 35) {
            setCategory('Ожирение I степени');
        } else if (bmiValue < 40) {
            setCategory('Ожирение II степени');
        } else {
            setCategory('Ожирение III степени (морбидное)');
        }
    };

    const resetCalculator = () => {
        setWeight('');
        setHeight('');
        setBmi(null);
        setCategory('');
    };

    return (
        <section className="relative w-full py-16 bg-gradient-to-b from-white to-gray-50">
            <div className="container mx-auto px-4 sm:px-6 lg:px-32">
                {/* Заголовок секции */}
                <div className="text-center mb-12">
                    <h1 className="text-2xl md:text-3xl font-bold text-red-600 mb-4">
                        Калькулятор ИМТ
                    </h1>
                    <p className="text-gray-600 max-w-2xl mx-auto">
                        Индекс массы тела (ИМТ) — это показатель, позволяющий оценить соответствие веса человека его росту.
                    </p>
                </div>

                <div className="max-w-md mx-auto bg-white rounded-2xl shadow-lg p-6 md:p-8 border border-gray-200">
                    {/* Поле для веса */}
                    <div className="mb-8">
                        <label className="block text-lg font-bold text-gray-900 mb-3">
                            Вес (кг)
                        </label>
                        <input
                            type="number"
                            value={weight}
                            onChange={(e) => setWeight(e.target.value)}
                            placeholder="Введите ваш вес"
                            className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-red-500 focus:ring-2 focus:ring-red-200 outline-none transition-all"
                            min="30"
                            max="250"
                        />
                        <p className="text-gray-500 text-sm mt-2">Пример: 70</p>
                    </div>

                    {/* Поле для роста */}
                    <div className="mb-10">
                        <label className="block text-lg font-bold text-gray-900 mb-3">
                            Рост (см)
                        </label>
                        <input
                            type="number"
                            value={height}
                            onChange={(e) => setHeight(e.target.value)}
                            placeholder="Введите ваш рост"
                            className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-red-500 focus:ring-2 focus:ring-red-200 outline-none transition-all"
                            min="100"
                            max="250"
                        />
                        <p className="text-gray-500 text-sm mt-2">Пример: 175</p>
                    </div>

                    {/* Кнопки */}
                    <div className="flex flex-col sm:flex-row gap-4 mb-10">
                        <button
                            onClick={calculateBMI}
                            className="flex-1 bg-red-600 hover:bg-red-700 text-white font-bold py-3 px-6 rounded-lg transition-colors shadow-md hover:shadow-lg"
                        >
                            Рассчитать ИМТ
                        </button>
                        <button
                            onClick={resetCalculator}
                            className="flex-1 bg-gray-200 hover:bg-gray-300 text-gray-800 font-bold py-3 px-6 rounded-lg transition-colors"
                        >
                            Сбросить
                        </button>
                    </div>

                    {/* Результат */}
                    {bmi && (
                        <div className="mt-8 p-6 bg-gradient-to-r from-red-50 to-orange-50 rounded-xl border border-red-100">
                            <h3 className="text-xl font-bold text-gray-900 mb-4 text-center">
                                Результат расчета
                            </h3>
                            <div className="text-center">
                                <div className="text-5xl font-bold text-red-600 mb-2">{bmi}</div>
                                <div className="text-lg font-semibold text-gray-800 mb-4">{category}</div>

                                {/* Шкала ИМТ */}
                                <div className="mt-6">
                                    <div className="flex justify-between text-sm text-gray-600 mb-2">
                                        <span>Недостаточный вес</span>
                                        <span>Нормальный</span>
                                        <span>Избыточный</span>
                                        <span>Ожирение</span>
                                    </div>
                                    <div className="h-4 bg-gradient-to-r from-blue-400 via-green-400 via-yellow-400 to-red-500 rounded-full relative">
                                        <div
                                            className="absolute top-1/2 transform -translate-y-1/2 w-4 h-6 bg-gray-900 rounded-full -ml-2"
                                            style={{
                                                left: `${Math.min(Math.max((parseFloat(bmi) - 15) / (40 - 15) * 100, 0), 100)}%`
                                            }}
                                        ></div>
                                    </div>
                                    <div className="flex justify-between text-xs text-gray-500 mt-2">
                                        <span>18.5</span>
                                        <span>25</span>
                                        <span>30</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )}

                    {/* Таблица категорий */}
                    <div className="mt-10">
                        <h4 className="font-bold text-gray-900 mb-3">Категории ИМТ:</h4>
                        <div className="space-y-2 text-sm">
                            <div className="flex items-center">
                                <div className="w-4 h-4 bg-blue-400 rounded mr-3"></div>
                                <span>Менее 18.5 — Недостаточный вес</span>
                            </div>
                            <div className="flex items-center">
                                <div className="w-4 h-4 bg-green-400 rounded mr-3"></div>
                                <span>18.5–24.9 — Нормальный вес</span>
                            </div>
                            <div className="flex items-center">
                                <div className="w-4 h-4 bg-yellow-400 rounded mr-3"></div>
                                <span>25–29.9 — Избыточный вес</span>
                            </div>
                            <div className="flex items-center">
                                <div className="w-4 h-4 bg-red-400 rounded mr-3"></div>
                                <span>Более 30 — Ожирение</span>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Дополнительная информация */}
                <div className="max-w-2xl mx-auto mt-12 text-center text-gray-600">
                    <p className="mb-4">
                        <strong>ИМТ ≥ 30</strong> может быть показанием для консультации с бариатрическим хирургом.
                    </p>
                    <p>
                        Калькулятор предоставляет ориентировочные данные. Для точной диагностики обратитесь к специалисту.
                    </p>
                </div>
            </div>
        </section>
    );
};

export default CalcSection